body, h1, h2, h3, h4, h5, h6, p, blockquote, pre, hr, dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0
}

body {
    font: 400 18px/1.8 "Apple SD Gothic Neo", "Nanum Barun Gothic", "Nanum Gothic", Verdana, Arial, "Malgun Gothic", Dotum, sans-serif;
    color: #212121;
    background-color: #fff;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -webkit-font-feature-settings: "kern" 1;
    -moz-font-feature-settings: "kern" 1;
    -o-font-feature-settings: "kern" 1;
    font-feature-settings: "kern" 1;
    font-kerning: normal
}

.selected {
    font-weight: bold;
    color: #021E7F;
}

h1, h2, h3, h4, h5, h6, p, blockquote, pre, ul, ol, dl, figure {
    margin-bottom: 15px
}

img {
    max-width: 100%;
    vertical-align: middle
}

figure > img {
    display: block
}

figcaption {
    font-size: 15.75px
}

ul, ol {
    margin-left: 30px
}

li > ul, li > ol {
    margin-bottom: 0
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 900
}

a:not(.menu-link):not(.head) {
    color: #02327f;
    font-weight: bold;
    text-decoration: none;
}

a:visited:not(.menu-link):not(.head) {
    color: #02327f;
    font-weight: bold;
}

a:hover:not(.menu-link):not(.head) {
    color: #212121
}

blockquote {
    color: #666;
    border-left: 4px solid #b3b3b3;
    padding-left: 15px
}

blockquote > :last-child {
    margin-bottom: 0
}

pre, code {
    background-color: #000;
    color: #fff;
    font-family: 'menlo', monospace
}

code {
    padding: 1px 5px
}

pre {
    padding: 8px 12px;
    overflow-x: auto
}

pre > code {
    border: 0;
    padding-right: 0;
    padding-left: 0
}

.wrapper {
    max-width: -webkit-calc(980px - (30px * 2));
    max-width: calc(980px - (30px * 2));
    margin-right: auto;
    margin-left: auto;
    padding-right: 30px;
    padding-left: 30px
}

@media screen and (max-width: 800px) {
    .wrapper {
        max-width: -webkit-calc(980px - (30px));
        max-width: calc(980px - (30px));
        padding-right: 15px;
        padding-left: 15px
    }
}

.content-layout {
    max-width: -webkit-calc(980px - (30px * 2));
    max-width: calc(980px - (30px * 2));
    margin-right: auto;
    margin-left: auto;
    margin-top: 30px;
    padding-right: 30px;
    padding-left: 30px
}

@media screen and (max-width: 800px) {
    .content-layout {
        max-width: -webkit-calc(980px - (30px));
        max-width: calc(980px - (30px));
        padding-right: 15px;
        padding-left: 15px
    }
}

.wrapper:after, .content-layout:after, .footer-col-wrapper:after {
    content: "";
    display: table;
    clear: both
}

.icon > svg {
    display: inline-block;
    width: 16px;
    height: 16px;
    vertical-align: middle
}

.icon > svg path {
    fill: #666
}

.gravatar {
    border-radius: 50%;
    margin-left: 10px
}

.btn {
    display: inline-block;
    padding: 0.7em 1em;
    font-size: 0.8em;
    border: 1px solid #eee
}

.post table {
    border-collapse: collapse;
    border-spacing: 0;
    border-color: #000000
}

.post table td, .post table th {
    border: 1px solid #d0d0d0;
    padding: 5px 5px
}

.post table th {
    font-weight: normal;
    background: #EEEEEE
}

.site-header {
    border-bottom: 1px solid #eeeeee;
    height: 65px;
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #ffffff;
    z-index: 10
}

.site-title {
    float: left
}

.site-title .site-logo {
    width: 160px;
    height: 66px;
    display: inline-block;
    background: url("../images/common/logo.jpg") no-repeat center center;
    background-size: 80% 80%;
    text-indent: -9999px
}

.site-title .site-title {
    float: right;
    font-weight: 600;
    margin: 3px 0 0 0px;
    height: 66px;
    line-height: 66px;
    display: inline-block;
    vertical-align: middle;
    font-size: 18px
}

.site-title, .site-title:visited {
    color: #262626
}

.site-nav {
    float: right;
    line-height: 65px
}

.site-nav .menu-icon {
    display: none
}

.site-nav .page-link {
    display: inline-block;
    color: #212121
}

.site-nav .page-link:hover {
    color: red
}

.site-nav .page-link:not(:last-child) {
    margin-right: 20px
}

@media screen and (max-width: 600px) {
    .site-nav {
        position: absolute;
        top: 9px;
        right: 15px;
        background-color: #fff;
        border: 1px solid #b3b3b3;
        border-radius: 5px;
        text-align: right
    }

    .site-nav .menu-icon {
        display: block;
        float: right;
        width: 36px;
        height: 26px;
        line-height: 0;
        padding-top: 10px;
        text-align: center
    }

    .site-nav .menu-icon > svg {
        width: 18px;
        height: 15px
    }

    .site-nav .menu-icon > svg path {
        fill: #262626
    }

    .site-nav .trigger {
        clear: both;
        display: none
    }

    .site-nav:hover .trigger {
        display: block;
        padding-bottom: 5px
    }

    .site-nav .page-link {
        display: block;
        padding: 5px 10px;
        margin-left: 20px
    }

    .site-nav .page-link:not(:last-child) {
        margin-right: 0
    }
}

.site-footer {
    text-align: center;
    color: #b3b3b3;
    padding: 30px 0
}

.footer-heading {
    font-size: 18px;
    margin-bottom: 15px
}

.contact-list, .social-media-list {
    list-style: none;
    margin-left: 0
}

.footer-col-wrapper {
    font-size: 15px;
    color: #666;
    margin-left: -15px
}

.footer-col {
    float: left;
    margin-bottom: 15px;
    padding-left: 15px
}

.footer-col-1 {
    width: -webkit-calc(35% - (30px / 2));
    width: calc(35% - (30px / 2))
}

.footer-col-2 {
    width: -webkit-calc(20% - (30px / 2));
    width: calc(20% - (30px / 2))
}

.footer-col-3 {
    width: -webkit-calc(45% - (30px / 2));
    width: calc(45% - (30px / 2))
}

@media screen and (max-width: 800px) {
    .footer-col-1, .footer-col-2 {
        width: -webkit-calc(50% - (30px / 2));
        width: calc(50% - (30px / 2))
    }

    .footer-col-3 {
        width: -webkit-calc(100% - (30px / 2));
        width: calc(100% - (30px / 2))
    }
}

@media screen and (max-width: 600px) {
    .footer-col {
        float: none;
        width: -webkit-calc(100% - (30px / 2));
        width: calc(100% - (30px / 2))
    }
}

.page-content {
    padding: 65px 0 30px
}

.page-content .wrapper {
    max-width: 100%;
    padding: 0
}

.page-heading {
    font-size: 20px
}

@keyframes fade {
    0%, 45.833333%, 100% {
        opacity: 0
    }
    8.3333%, 37.49999% {
        opacity: 1
    }
}

.main-banner {
    position: relative;
    width: 100%;
    height: 383px
}

@media screen and (max-width: 600px) {
    .main-banner {
        height: 190px
    }
}

.main-banner .dimmed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background: rgba(0, 0, 0, 0.4)
}

.main-banner .dimmed .description {
    float: left;
    margin-top: 63px
}

@media screen and (max-width: 600px) {
    .main-banner .dimmed .description {
        margin-top: 17px
    }
}

.main-banner .dimmed .content-layout {
    position: relative;
    height: 100%;
    margin-top: 0;
    overflow: hidden
}

.main-banner .dimmed .content-layout .wrap-img {
    position: relative;
    width: 244px;
    height: 100%;
    margin-right: -15px;
    float: right;
    overflow: hidden
}

@media screen and (max-width: 600px) {
    .main-banner .dimmed .content-layout .wrap-img {
        width: 100px;
        height: 100%;
        padding-left: 10px
    }
}

.main-banner .dimmed .content-layout .wrap-img img {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 244px;
    height: 249px
}

@media screen and (max-width: 600px) {
    .main-banner .dimmed .content-layout .wrap-img img {
        width: 100px;
        height: 109px
    }
}

.main-banner .dimmed .text {
    color: #ffffff
}

.main-banner .dimmed span.text {
    font-size: 24px;
    font-weight: 500;
    line-height: 33px;
    letter-spacing: -0.5px
}

@media screen and (max-width: 600px) {
    .main-banner .dimmed span.text {
        font-size: 14px;
        line-height: 17px
    }
}

.main-banner .dimmed p.text {
    margin: 6px 0 30px;
    font-size: 56px;
    font-weight: bold;
    line-height: 72px;
    letter-spacing: -0.5px
}

@media screen and (max-width: 600px) {
    .main-banner .dimmed p.text {
        margin: 0 0 8px;
        font-size: 28px;
        line-height: 37px
    }
}

.main-banner .dimmed a[role=button] {
    padding: 12px 22px;
    font-size: 21px;
    font-weight: 500;
    letter-spacing: -0.5px;
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid #ffffff;
    border-radius: 2px;
    color: #ffffff
}

@media screen and (max-width: 600px) {
    .main-banner .dimmed a[role=button] {
        font-size: 14px;
        padding: 8px 14px
    }
}

.main-banner .dimmed a[role=button]:hover, .main-banner .dimmed a[role=button]:active {
    background-color: rgba(255, 255, 255, 0.4)
}

.main-banner .bg-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center center;
    animation-name: fade;
    animation-duration: 10.8s;
    animation-iteration-count: infinite
}

.main-banner .bg-item:nth-of-type(1) {
    background-image: url("../images/banner/man-window.jpg");
    z-index: 1;
    animation-delay: 10.8s
}

.main-banner .bg-item:nth-of-type(2) {
    background-image: url("../images/banner/city-tower.jpg");
    z-index: 2;
    animation-delay: 3.6s;
    opacity: 0
}

.main-banner .bg-item:nth-of-type(3) {
    background-image: url("../images/banner/woman-shining.jpg");
    z-index: 3;
    animation-delay: 7.2s;
    opacity: 0
}

.list {
    float: left;
    width: 65%
}

@media screen and (max-width: 600px) {
    .list {
        width: 100%
    }
}

.list-module {
    padding: 30px 0;
    border-bottom: 1px solid #dddddd
}

.list-module:last-of-type {
    border-bottom: none
}

.list-module h2 {
    margin: 10px 0 0
}

.post-meta {
    font-size: 15.75px;
    color: #262626
}

.post-link {
    display: block;
    font-size: 2em;
    line-height: 1.5;
    color: #212121
}

.post-link:hover {
    text-decoration: none
}

.post-link:visited {
    color: #212121
}

.post-description {
    font-size: 0.8em;
    color: #424242
}

.page-aside {
    float: right;
    width: 25%;
    box-sizing: border-box;
    padding: 30px
}

.page-aside section {
    margin-bottom: 30px
}

.page-aside section ul {
    list-style: none;
    margin-left: 0
}

.page-aside section ul li {
    font-size: 16px
}

@media screen and (max-width: 600px) {
    .page-aside {
        display: none
    }
}

.post-header {
    margin: 60px 0 30px
}

.post-title {
    font-size: 2em;
    line-height: 1.5
}

.post-content {
    margin-bottom: 30px;
    word-break: break-word
}

.post-content h2, .post-content h3, .post-content h4, .post-content h5, .post-content h6 {
    margin-top: 60px
}

.post-content a {
    text-decoration: underline;
    color: #FFFFFF
}

.btn-block {
    display: block;
    margin: 100px auto 0;
    text-align: center
}

#pagination {
    margin: 0;
    padding: 33px 0;
    text-align: center;
}

#pagination > li {
    list-style: none;
    display: inline-block;
    height: 20px;
    width: 20px;
    color: #1e1e1e;
    font-size: 14px;
    line-height: 18px; /* 18px = 20(height) - 2(border) */
    vertical-align: middle;
}

/* pagination */

.sr-only {
    position: absolute;
    top: -9999px;
    left: -9999px
}

#pagination > li + li {
    margin-left: 10px;
}

#pagination > li.active, #pagination > li:hover {
    background-color: #e6e6e6;
    border-radius: 4px;
}

#pagination > li > a {
    color: #1e1e1e;
    width: 100%;
    height: 100%;
    display: block;
}

#page-prev {
    background: url(../images/common/icon_pre.png) no-repeat center;
    background-size: 6px 11px;
}

#page-next {
    background: url(../images/common/icon_next.png) no-repeat center;
    background-size: 6px 11px;
}

